.player {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: center;
}

.player video {
  width: 90%;
  max-width: 900px;
  height: auto;
  border: 4px solid var(--color-bcg-accent);
}

.player.hide{
    display: none;
}